import test from '@/common/test';
import $N from '@/common/unit';
import htmlParser from '@/common/html-parser';
import uniLoadMore from '@/components/uni-ui/uni-load-more/uni-load-more.vue';
import uniStatusBar from '@/components/uni-ui/uni-status-bar/uni-status-bar.vue';
import uniDrawer from '@/components/uni-ui/uni-drawer/uni-drawer.vue';
export default {
	components: {
		uniStatusBar,
		uniLoadMore,
		uniDrawer
	},
	data() {
		return {
			calHeight: 0,
			setStatus: false,
			catalogStatus: false,
			typeFaceStatus: false,
			moreStatus: false,
			chapterIndex: 0, // 当前章节标识
			novalName: test.name, // 小说名称
			chapterCatalog: test.chapterCatalog ,// 小说目录
			LoadedChapters: [] ,// 已经加载的章节
			myFontSize: uni.getStorageSync('myFontSize') ? uni.getStorageSync('myFontSize') : 20,
			myLineHeight: uni.getStorageSync('myLineHeight') ? uni.getStorageSync('myLineHeight') : 45,
			brightNess: 0, // 亮度值
			themes:[
				{
					id: 'blueTheme',
					name: '天蓝'
				},
				{
					id: 'eyeHelpTheme',
					name: '护眼'
				},
				{
					id: 'lightGretTheme',
					name: '淡灰'
				},
				{
					id: 'morningTheme',
					name: '早晨'
				},
				{
					id: 'nightTheme',
					name: '夜间'
				}
			],
			themeIndex: (uni.getStorageSync('themeIndex') === 3 || !uni.getStorageSync('themeIndex')) ? 3 : uni.getStorageSync('themeIndex'),
		}
	},
	computed: {
		// 当前章节标题
		curChapterTitle() {
			return this.chapterCatalog[this.chapterIndex].title 
		},
		// 当前主题
		curTheme(){
			return this.themes[this.themeIndex].id
		}
	},
	methods: {
		// 退出
		quit(){
			uni.navigateBack({
				delta: 1
			});
		},
		// 改变设置部分状态
		changeSetStatus(){
			if (!this.setStatus) {
				if (this.typeFaceStatus || this.moreStatus) {
					this.changeTypeFaceStatus(false);
					this.changeMoreStatus(false);
					return
				}
			}
			this.setStatus = !this.setStatus
		},
		// 改变目录部分状态
		changeCatalogStatus(Bol){
			this.catalogStatus = Bol;
			if (this.catalogStatus) this.changeSetStatus()
		},
		// 改变字体设置部分状态
		changeTypeFaceStatus(Bol){
			this.typeFaceStatus = Bol;
			if (this.typeFaceStatus) this.changeSetStatus()
		},
		// 改变更多设置部分状态
		changeMoreStatus(Bol){
			this.moreStatus = Bol;
			if (this.moreStatus) this.changeSetStatus()
		},
		
		// 初始化
		init(id){
			let curIndex = this.chapterCatalog.findIndex(item => item.id === Number(id));
			this.changeIndex(curIndex);
			this.preLoad(); // 预加载
			this.deplyLoad() // 延迟加载
		},
		// 预加载
		preLoad(){
			test.chapterCatalog.forEach(item => this.LoadedChapters.push({
				id: item.id,
				title: item.title,
				text: ''
			}))
		},
		// 延迟加载
		deplyLoad(){
			let index = this.chapterIndex;
			if(this.LoadedChapters[index].text === ''){
				setTimeout(() => this.LoadedChapters[index].text =htmlParser(test.content[index].text), 1000)
			}
		},
		// 改变当前章节标识
		changeIndex(index){
			this.chapterIndex = index
		},
		// 滑动改变章节标识
		swiperChangeChapter(e){
			this.changeIndex(e.detail.current)
			this.deplyLoad()
		},
		// 点击目录章节跳转对应章节
		toPointChapter(id){
			let curIndex = this.chapterCatalog.findIndex(item => item.id === id);
			if (this.chapterIndex === curIndex) return;
			this.changeIndex(curIndex);
			this.deplyLoad()
		},
		// 改变字体大小
		changeFontSize(e){
			this.myFontSize = e.detail.value;
			uni.setStorageSync('myFontSize', this.myFontSize)
		},
		// 改变行文间距
		changeLineHeight(e){
			this.myLineHeight = e.detail.value;
			uni.setStorageSync('myLineHeight', this.myLineHeight)
		},
		// 获取亮度
		getBrightNess(){
			uni.getScreenBrightness({
				success: e => this.brightNess = Math.floor(e.value) / 16 * 100
			})
		},
		// 设置亮度
		setBrightNess(e){
			let newVal = e.detail.value;
			this.brightNess = newVal;
			uni.setScreenBrightness({
				value: newVal * 16 / 100
			})
		},
		// 更改主题标识
		changeThemeIndex(id){
			let curIndex = this.themes.findIndex(theme => theme.id === id);
			this.themeIndex = curIndex
			
			// 写入主题缓存
			uni.setStorageSync('themeIndex',curIndex)
		},
		isNight(){
			this.themeIndex !== 4 ? this.changeThemeIndex('nightTheme') : this.changeThemeIndex('morningTheme')
		}
	},
	onLoad(e) {
		uni.getSystemInfo({
			success:res=>{
				// px
				this.calHeight = $N.Torpx(res.windowHeight - res.statusBarHeight);
			}
		})
		// console.log(e)
		let id = e.chapterID;
		this.init(id)
	},
	created() {

		// 获取屏幕亮度
		this.getBrightNess()

	},
	
}
